import React from 'react'
import Card from "./Card"
import "./Service.css"
import { Carousel } from 'react-bootstrap'
import img1 from './images/tr1.jpg'
import img2 from './images/tr2.jpg'
import img3 from './images/tr3.jpg'



/**
* @author
* @function Service
**/

const Service = () => {
  return (
    <>
      <Carousel>
        <Carousel.Item>
          <img
            className="d-block w-100"
            src={require("./images/tr1.jpg")}
            alt="First slide"
            style={{height:"90vh"}}
          />
          <Carousel.Caption>
            <h3>By Road</h3>
            <p>Transport facility</p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="d-block w-100"
            src={img2}
            alt="Second slide"
            style={{height:"90vh"}}
          />

          <Carousel.Caption>
            <h3>By Airway</h3>
            <p>Transport facility</p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="d-block w-100"
            src={img3}
            alt="Third slide"
            style={{height:"90vh"}}
          />

          <Carousel.Caption>
            <h3>By Ship</h3>
            <p>Transport facility</p>
          </Carousel.Caption>
        </Carousel.Item>
      </Carousel>



    </>
  )

}

export default Service